import React from "react";
import { AppBar, Toolbar, IconButton } from "@material-ui/core";
import { withRouter } from "react-router";
import { useTheme, ThemeProvider } from "@material-ui/core/styles";
import CancelIcon from "@material-ui/icons/Cancel";
import MenuIcon from "@material-ui/icons/Menu";
import { styled } from "@material-ui/core/styles";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Switch from "@material-ui/core/Switch";
import { Link } from "react-tiger-transition";
// import { Link } from "react-router-dom";
import { useStore } from "./contexts/theme.context";
import { extractTitle } from "./utils/extractTitle";
import styles from "./styles/NavbarStyles";

const screenStyle = {
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
};
const MaterialUISwitch = styled(Switch)(({ theme }) => ({
  width: 62,
  height: 30,
  padding: 7,
  "& .MuiSwitch-switchBase": {
    margin: 1,
    padding: 0,
    transform: "translateX(6px)",
    "&.Mui-checked": {
      color: "#fff",
      transform: "translateX(26px)",
      "& .MuiSwitch-thumb:before": {
        backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
          "#fff"
        )}" d="M4.2 2.5l-.7 1.8-1.8.7 1.8.7.7 1.8.6-1.8L6.7 5l-1.9-.7-.6-1.8zm15 8.3a6.7 6.7 0 11-6.6-6.6 5.8 5.8 0 006.6 6.6z"/></svg>')`,
      },
      "& + .MuiSwitch-track": {
        opacity: 1,
        backgroundColor: theme.palette.mode === "dark" ? "#8796A5" : "#aab4be",
      },
    },
  },
  "& .MuiSwitch-thumb": {
    backgroundColor: theme.palette.mode === "dark" ? "#003892" : "#001e3c",
    width: 28,
    height: 28,
    "&:before": {
      content: "''",
      position: "absolute",
      width: "100%",
      height: "100%",
      left: 0,
      top: 0,
      backgroundRepeat: "no-repeat",
      backgroundPosition: "center",
      backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
        "#fff"
      )}" d="M9.305 1.667V3.75h1.389V1.667h-1.39zm-4.707 1.95l-.982.982L5.09 6.072l.982-.982-1.473-1.473zm10.802 0L13.927 5.09l.982.982 1.473-1.473-.982-.982zM10 5.139a4.872 4.872 0 00-4.862 4.86A4.872 4.872 0 0010 14.862 4.872 4.872 0 0014.86 10 4.872 4.872 0 0010 5.139zm0 1.389A3.462 3.462 0 0113.471 10a3.462 3.462 0 01-3.473 3.472A3.462 3.462 0 016.527 10 3.462 3.462 0 0110 6.528zM1.665 9.305v1.39h2.083v-1.39H1.666zm14.583 0v1.39h2.084v-1.39h-2.084zM5.09 13.928L3.616 15.4l.982.982 1.473-1.473-.982-.982zm9.82 0l-.982.982 1.473 1.473.982-.982-1.473-1.473zM9.305 16.25v2.083h1.389V16.25h-1.39z"/></svg>')`,
    },
  },
  "& .MuiSwitch-track": {
    opacity: 1,
    backgroundColor: theme.palette.mode === "dark" ? "#8796A5" : "#aab4be",
    borderRadius: 20 / 2,
  },
}));
function Navbar({ history }) {
  const { isDarkTheme, changeTheme } = useStore();
  const classes = styles(isDarkTheme);
  const theme = useTheme();
  const title = extractTitle(history.location.pathname);
  if (title.length === 0) {
    title[0] = "Home";
  }
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handleChange = () => {
    changeTheme();
  };
  const links = ["Home", "About", "Projects", "Contact"];

  return (
    <ThemeProvider theme={theme}>
      <div className={classes.root}>
        <AppBar position="absolute" color="transparent" className={classes.bar}>
          <Toolbar>
            <IconButton
              edge="start"
              className={classes.menuButton}
              aria-label="menu"
              onClick={handleOpen}
              style={{ display: title[0] === "Home" ? "none" : "" }}
            >
              <MenuIcon />
            </IconButton>
            {/* <Typography
            variant="h6"
            className={classes.title}
            style={{ textAlign: "right" }}
          >
            {title.map((t, i) => {
              return i === title.length - 1 ? `${t}` : `${t} - `;
            })}
          </Typography> */}
            <div className={classes.theme}>
              <FormGroup>
                <FormControlLabel
                  style={{
                    color: isDarkTheme ? "#fcfcfc" : "#111211",
                  }}
                  control={
                    <MaterialUISwitch
                      sx={{ m: 1 }}
                      defaultChecked
                      onChange={handleChange}
                    />
                  }
                  label={isDarkTheme ? "Dark" : "Light"}
                />
              </FormGroup>
            </div>
          </Toolbar>
          <div
            className={`${classes.cMsg} ${open && classes.showMessage} `}
            style={{ display: open ? "" : "none" }}
          >
            <CancelIcon onClick={handleClose} className={classes.cancelIcon} />

            {/* side projects come in link */}
            <div className={classes.links}>
              {links.map((link, index) => (
                <Link
                  className={classes.link}
                  transition="scaleUp"
                  key={index}
                  to={`/${link === "Home" ? `` : `${link.toLowerCase()}`}`}
                  onClick={handleClose}
                  style={
                    link === title[0]
                      ? {
                          color: `${isDarkTheme ? `#303F9F` : `#c4cdff`}`,
                          ...screenStyle,
                        }
                      : {
                          color: `${isDarkTheme ? `black` : `white`}`,
                          ...screenStyle,
                        }
                  }
                >
                  {link}
                </Link>
              ))}
            </div>
          </div>
          <div
            onClick={handleOpen}
            className={`${classes.cOverlay} ${open && classes.showOverlay} `}
          />
        </AppBar>
      </div>
    </ThemeProvider>
  );
}

export default withRouter(Navbar);
